import Request from './request'

//  token
export const needTokenRequest = new Request({
  baseURL: 'http://localhost:8080/api',
  timeout: 10 * 1000,
  headers: {
    token: localStorage.getItem('token'),
  },
})
// token
export const noTokenRequest = new Request({
  baseURL: 'http://localhost:8080/api',
  timeout: 10 * 1000,
})
