import { needTokenRequest } from '../..'

export const getHistoryMessages = () => {
  return needTokenRequest.request({
    url: '/chat',
    method: 'GET',
  })
}

/**
 * 
 * @returnsgroupList
 */
export const getGroupList = () => {
  return needTokenRequest.request({
    url: '/group',
    method: 'GET',
  })
}

export const getGroupMessageList = (groupId: any) => {
  return needTokenRequest.request({
    url: '/group/message',
    method: 'POST',
    data: {
      groupId,
    },
  })
}
