import { loginRequest } from '@/service/request/module/user'
import { createAsyncThunk, createSlice } from '@reduxjs/toolkit'

interface UserInfo {
  username: string
  password: string
}

export const loginRequestMutation = createAsyncThunk(
  'user/loginRequest',
  async (payload: UserInfo) => {
    const res = await loginRequest(payload.username, payload.password)
    return res
  }
)

const counterSlicer = createSlice({
  name: 'user',
  initialState: {
    username: '',
    token: '',
  },
  reducers: {},
  extraReducers(builder) {
    builder.addCase(
      loginRequestMutation.fulfilled,
      (state, { meta, payload }) => {
        state.token = payload.data as string
        state.username = meta.arg.username
        localStorage.setItem('token', state.token)
        localStorage.setItem('username', meta.arg.username)
      }
    )
  },
})
export default counterSlicer.reducer
