import React, { lazy } from 'react'
import { Navigate } from 'react-router-dom'
import type { RouteObject } from 'react-router-dom'

const Login = lazy(() => import('@/views/login'))
const Chat = lazy(() => import('@/views/chat/index'))
const SingleChatPage = lazy(() => import('@/views/chat/singleChatPage'))
const GroupChatPage = lazy(() => import('@/views/chat/groupChatPage'))

const routes: RouteObject[] = [
  {
    path: '/',
    element: <Navigate to="/login" />,
  },
  {
    path: '/login',
    element: <Login></Login>,
  },
  {
    path: '/chat',
    element: <Chat></Chat>,
    children: [
      {
        path: '/chat/single/:chatId',
        element: <SingleChatPage></SingleChatPage>,
      },
      {
        path: '/chat/group/:chatId',
        element: <GroupChatPage></GroupChatPage>,
      },
    ],
  },
]
export default routes
