import type {
  AxiosInstance,
  AxiosResponse,
  InternalAxiosRequestConfig,
} from 'axios'
import axios from 'axios'
import type { InterceptorsAxiosRequestConfig } from './type'

class Request {
  private instance: AxiosInstance

  constructor(config: InterceptorsAxiosRequestConfig) {
    this.instance = axios.create(config)
    // 
    // configconfig
    this.instance.interceptors.request.use(
      (config: InternalAxiosRequestConfig) => {
        return config
      },
      (error) => {
        return error
      }
    )
    this.instance.interceptors.response.use(
      (res: AxiosResponse) => {
        const { data } = res
        return data
      },
      (error) => {
        return error
      }
    )
    // 
    this.instance.interceptors.request.use(
      config.interceptors?.requestInterceptorFailFn,
      config.interceptors?.requestInterceptorFailFn
    )
    this.instance.interceptors.response.use(
      config.interceptors?.reqponseInterceptorSuccessFn,
      config.interceptors?.requestInterceptorFailFn
    )
  }

  public request<T>(config: InterceptorsAxiosRequestConfig) {
    // 
    this.instance.interceptors.request.use(
      config.interceptors?.requestInterceptorFailFn,
      config.interceptors?.requestInterceptorFailFn
    )
    this.instance.interceptors.response.use(
      config.interceptors?.reqponseInterceptorSuccessFn,
      config.interceptors?.requestInterceptorFailFn
    )
    return new Promise<AxiosResponse<T>>((resolve, reject) => {
      this.instance
        .request(config)
        .then((res: AxiosResponse<T>) => {
          resolve(res)
        })
        .catch((err) => {
          reject(err)
        })
    })
  }
}

export default Request
