import { createAsyncThunk, createSlice } from '@reduxjs/toolkit'
import {
  getHistoryMessages,
  getGroupList,
  getGroupMessageList,
} from '@/service/request/module/message'

export const messageRequestMutation = createAsyncThunk(
  'message/messageRequest',
  async () => {
    const res = await getHistoryMessages()
    return res
  }
)

export const groupRequestMutation = createAsyncThunk(
  'message/groupRequest',
  async () => {
    const res = await getGroupList()
    return res
  }
)
export const groupMessageRequestMutation = createAsyncThunk(
  'message/groupMessageRequest',
  async (groupId: string) => {
    const res = await getGroupMessageList(groupId)
    return res
  }
)

const messageSlicer = createSlice({
  name: 'message',
  initialState: {
    friendsList: [] as FriendsList,
    messagesList: [] as MessagesList,
    groupList: [] as GroupList,
    groupMessageList: [] as GroupMessageList,
  },
  reducers: {
    addMessage(state, { payload }) {
      state.messagesList.push(payload)
    },
    addGroupMessage(state, { payload }) {
      state.groupMessageList.push(payload)
    },
  },
  extraReducers(builder) {
    builder.addCase(messageRequestMutation.fulfilled, (state, { payload }) => {
      const { data } = payload
      const { friendsList, messagesList } = data as any
      state.friendsList = friendsList
      state.messagesList = messagesList
    })
    builder.addCase(groupRequestMutation.fulfilled, (state, { payload }) => {
      const { data } = payload

      state.groupList = data as GroupList
    })

    builder.addCase(
      groupMessageRequestMutation.fulfilled,
      (state, { payload }) => {
        const { data } = payload as any

        state.groupMessageList = data
      }
    )
  },
})
export const { addMessage, addGroupMessage } = messageSlicer.actions
export default messageSlicer.reducer
